import { notFound } from 'next/navigation';
import { fetchJob } from '@/lib/api';
import { Metadata } from 'next';

type Props = {
  params: Promise<{ slug: string }>;
};

export async function generateMetadata({ params }: Props): Promise<Metadata> {
  const { slug } = await params;
  const job = await fetchJob(slug);
  
  if (!job) {
    return {
      title: 'Job Not Found | Jobslly',
    };
  }
  
  const title = `${job.title} Job at ${job.company} in ${job.location} | Jobslly`;
  const description = `Apply for the ${job.title} job at ${job.company} in ${job.location}. View eligibility, salary, skills, and apply online on Jobslly.`;
  
  return {
    title,
    description,
    keywords: `${job.title}, ${job.company}, ${job.location}, healthcare jobs, medical careers, job opportunities`,
    openGraph: {
      title: `${job.title} at ${job.company}`,
      description,
      type: 'website',
      url: `https://jobslly.com/jobs/${slug}`,
    },
    twitter: {
      card: 'summary_large_image',
      title: `${job.title} at ${job.company}`,
      description,
    },
    alternates: {
      canonical: `https://jobslly.com/jobs/${slug}`,
    },
    robots: {
      index: true,
      follow: true,
    },
  };
}

export default async function JobDetailPage({ params }: Props) {
  const { slug } = await params;
  const job = await fetchJob(slug);
  
  if (!job) {
    notFound();
  }
  
  // Generate JobPosting JSON-LD Schema
  const jsonLd = {
    '@context': 'https://schema.org',
    '@type': 'JobPosting',
    title: job.title,
    description: job.description?.replace(/<[^>]*>/g, '').substring(0, 500),
    datePosted: job.created_at,
    hiringOrganization: {
      '@type': 'Organization',
      name: job.company,
    },
    jobLocation: {
      '@type': 'Place',
      address: {
        '@type': 'PostalAddress',
        addressLocality: job.location,
        addressCountry: 'IN',
      },
    },
    employmentType: job.job_type?.toUpperCase().replace(' ', '_') || 'FULL_TIME',
    url: `https://jobslly.com/jobs/${slug}`,
  };
  
  return (
    <>
      {/* JSON-LD Schema */}
      <script
        type="application/ld+json"
        dangerouslySetInnerHTML={{ __html: JSON.stringify(jsonLd) }}
      />
      
      <div className="min-h-screen bg-gray-50">
        {/* Header */}
        <div className="bg-white border-b">
          <div className="max-w-7xl mx-auto px-4 py-8">
            <h1 className="text-4xl font-bold mb-4">{job.title}</h1>
            <div className="flex flex-wrap gap-4 text-gray-600">
              <div className="flex items-center gap-2">
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M19 21V5a2 2 0 00-2-2H7a2 2 0 00-2 2v16m14 0h2m-2 0h-5m-9 0H3m2 0h5M9 7h1m-1 4h1m4-4h1m-1 4h1m-5 10v-5a1 1 0 011-1h2a1 1 0 011 1v5m-4 0h4" />
                </svg>
                <span className="font-semibold">{job.company}</span>
              </div>
              <div className="flex items-center gap-2">
                <svg className="w-5 h-5" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M17.657 16.657L13.414 20.9a1.998 1.998 0 01-2.827 0l-4.244-4.243a8 8 0 1111.314 0z" />
                  <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 11a3 3 0 11-6 0 3 3 0 016 0z" />
                </svg>
                <span>{job.location}</span>
              </div>
              {job.job_type && (
                <div className="px-3 py-1 bg-blue-100 text-blue-800 rounded-full text-sm font-medium">
                  {job.job_type}
                </div>
              )}
            </div>
          </div>
        </div>
        
        {/* Main Content */}
        <div className="max-w-7xl mx-auto px-4 py-8">
          <div className="grid grid-cols-1 lg:grid-cols-3 gap-8">
            {/* Left Column - Job Details */}
            <div className="lg:col-span-2 space-y-6">
              {/* Description */}
              <div className="bg-white rounded-lg shadow p-6">
                <h2 className="text-2xl font-bold mb-4">Job Description</h2>
                <div 
                  className="prose max-w-none"
                  dangerouslySetInnerHTML={{ __html: job.description || 'No description available' }}
                />
              </div>
              
              {/* Requirements */}
              {job.requirements && job.requirements.length > 0 && (
                <div className="bg-white rounded-lg shadow p-6">
                  <h2 className="text-2xl font-bold mb-4">Requirements</h2>
                  <ul className="list-disc list-inside space-y-2">
                    {job.requirements.map((req: string, index: number) => (
                      <li key={index} className="text-gray-700">{req}</li>
                    ))}
                  </ul>
                </div>
              )}
              
              {/* Benefits */}
              {job.benefits && job.benefits.length > 0 && (
                <div className="bg-white rounded-lg shadow p-6">
                  <h2 className="text-2xl font-bold mb-4">Benefits</h2>
                  <ul className="list-disc list-inside space-y-2">
                    {job.benefits.map((benefit: string, index: number) => (
                      <li key={index} className="text-gray-700">{benefit}</li>
                    ))}
                  </ul>
                </div>
              )}
            </div>
            
            {/* Right Column - Job Info & Apply */}
            <div className="space-y-6">
              {/* Job Info Card */}
              <div className="bg-white rounded-lg shadow p-6 sticky top-4">
                <h3 className="text-xl font-bold mb-4">Job Information</h3>
                <div className="space-y-3">
                  {job.salary_min && (
                    <div>
                      <div className="text-sm text-gray-600">Salary</div>
                      <div className="font-semibold">
                        {job.currency} {job.salary_min}
                        {job.salary_max && ` - ${job.salary_max}`}
                      </div>
                    </div>
                  )}
                  
                  {job.categories && job.categories.length > 0 && (
                    <div>
                      <div className="text-sm text-gray-600 mb-2">Categories</div>
                      <div className="flex flex-wrap gap-2">
                        {job.categories.map((cat: string, index: number) => (
                          <span key={index} className="px-2 py-1 bg-gray-100 rounded text-sm">
                            {cat}
                          </span>
                        ))}
                      </div>
                    </div>
                  )}
                  
                  <div>
                    <div className="text-sm text-gray-600">Posted</div>
                    <div className="font-semibold">
                      {new Date(job.created_at).toLocaleDateString('en-US', {
                        year: 'numeric',
                        month: 'long',
                        day: 'numeric'
                      })}
                    </div>
                  </div>
                </div>
                
                {/* Apply Button */}
                <button className="w-full mt-6 bg-teal-600 hover:bg-teal-700 text-white font-bold py-3 px-6 rounded-lg transition duration-200">
                  Apply Now
                </button>
                
                <button className="w-full mt-3 border-2 border-teal-600 text-teal-600 hover:bg-teal-50 font-bold py-3 px-6 rounded-lg transition duration-200">
                  Save Job
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
}
